body {
  min-width: 320px;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  width: auto;
  margin: 0 auto;
  background-color: #000000;
  background-image: url("../img/bg-header-desktop@1x.jpg");
  background-repeat: no-repeat;
  background-size: cover; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .page-header {
      background-image: url("../img/bg-header-desktop@2x.jpg"); } }

@media (max-width: 1024px) and (min-width: 768px) {
  .page-header {
    background-image: url("../img/bg-header-tablet@1x.jpg"); } }
  @media (max-width: 1024px) and (min-width: 768px) and (min-resolution: 144dpi), (max-width: 1024px) and (min-width: 768px) and (min-resolution: 1.5dppx) {
    .page-header {
      background-image: url("../img/bg-header-tablet@2x.jpg"); } }

@media (max-width: 767px) {
  .page-header {
    background-image: url("../img/bg-header-mobile@1x.jpg"); } }
  @media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
    .page-header {
      background-image: url("../img/bg-header-mobile@2x.jpg"); } }

.site-nav {
  color: #ffffff;
  padding-bottom: 92px; }

.site-nav__button {
  display: block;
  width: 407px;
  height: 55px;
  border-radius: 5px;
  background-color: #00b2ff;
  cursor: pointer;
  text-decoration: none;
  color: #ffffff;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  font-family: "Roboto-Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  margin-left: 18%;
  box-sizing: border-box;
  padding: 17px 28px 10px 28px;
  margin-bottom: 93px; }

.site-nav__scroll {
  text-decoration: none;
  font-size: 14px;
  line-height: 16px;
  cursor: pointer;
  color: #ffffff;
  font-family: "PT Mono", "Courier New", monospace;
  font-style: normal;
  font-weight: normal;
  opacity: 0.5;
  display: inline-block;
  width: 93px;
  transform: rotate(-90deg);
  margin-left: 11.17%;
  margin-bottom: 40px;
  margin-top: 37px; }

@media (min-width: 320px) and (max-width: 1024px) {
  .site-nav__scroll {
