@media (min-width: 768px) and (max-width: 1024px) {
  .site-nav {
    padding-bottom: 70px; }
  .site-nav__button {
    width: 379px; } }

@media (max-width: 767px) {
  .site-nav {
    padding-bottom: 70px; }
  .site-nav__button {
    width: 89.7%;
    margin: 0 auto;
    position: relative; }
    .site-nav__button::after {
      position: static;
      content: "бесплатная консультация";
      top: 31%;
      left: 12%; }
  .site-nav__button span {
    display: none; } }

